@keyframes astro-bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-7px);
  }
}
@keyframes smoothScroll {
  0% {
    transform: translateY(-40px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes pulsed {
  0% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0.4); */
    transform: scale(1);
  }
  70% {
    /* box-shadow: 0 0 0 10px rgba(204,169,44, 0); */
    transform: scale(1.05);
  }
  100% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0); */
    transform: scale(1);
  }
}
.slick-prev,
.slick-next {
  font-family: 'Fontello';
  transition: all .3s linear;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.slick-prev {
  left: -10px;
}
.slick-next {
  right: 0;
}
.slick-prev:before,
.slick-next:before {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-25%);
  z-index: 10;
  color: #fff;
  font-size: 60px;
}
@media only screen and (min-width: 1600px) {
  .slick-prev:before,
  .slick-next:before {
    font-size: 120px;
  }
}
.slick-prev:before {
  left: 0;
}
.slick-next:before {
  right: 0;
}
.slick-dots {
  left: 50%;
  bottom: -40px;
  transform: translateX(-50%);
  border-image: transparent;
}
.slick-dots li {
  margin: 0 5px;
  width: 20px;
  height: 20px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 20px;
  width: 20px;
  font-size: 0px;
  color: #fff;
  padding: 5px;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
